.eidt-page-wrapper {
  display: flex;
  position: relative;
}
.eidt-page-wrapper .save-btn {
  position: absolute;
  right: 120px;
  top: 10px;
  background-color: #D7D4CD;
  border: 0px;
}
.eidt-page-wrapper .save-btn:hover {
  color: #D7D4CD;
  border: 1px solid #D7D4CD;
  background-color: white;
}
.eidt-page-wrapper .publish-btn {
  position: absolute;
  right: 20px;
  top: 10px;
  color: white;
  background-color: #F28B30;
  border: 0px;
}
.eidt-page-wrapper .publish-btn:hover {
  color: #F28B30;
  background-color: white;
  border: 1px solid #F28B30;
}
.eidt-page-wrapper .edit-partition-container {
  position: fixed;
  bottom: 0px;
  left: 20vw;
  display: flex;
  line-height: 30px;
  height: 34px;
}
.eidt-page-wrapper .edit-partition-container .edit-partition {
  color: #bec1c3;
  cursor: pointer;
  display: flex;
  padding-left: 8px;
  padding-right: 8px;
  font-size: 18px;
  margin-top: 1px;
  font-weight: 900;
}
.eidt-page-wrapper .edit-partition-container .edit-partition .edit-partition-icon {
  width: 18px;
  height: 30px;
  background: url(/static/media/partition.1bb4f8cb.svg) no-repeat center;
  background-size: contain;
  margin-right: 8px;
}
.eidt-page-wrapper .edit-partition-container-choosed .edit-partition {
  color: #fff;
  background-color: #DEDEDF;
}
.eidt-page-wrapper .edit-partition-container-choosed .edit-partition .edit-partition-icon {
  background: url(/static/media/partition_white.0625cd7d.svg) no-repeat center;
  background-size: contain;
}
.edit-partition-choice .ant-dropdown-menu {
  background: #f4f4f4;
  border-radius: 6px;
}
.edit-partition-choice .ant-dropdown-menu .ant-dropdown-menu-item {
  border-bottom: 1px solid #ddd;
}
.edit-partition-choice .ant-dropdown-menu .ant-dropdown-menu-item:last-child {
  border-bottom: 0px;
}
.edit-partition-choice .ant-dropdown-menu .ant-dropdown-menu-item:hover {
  background-color: #DEDEDF;
}
.editSpining {
  height: 100%;
}
.editSpining .ant-spin-container {
  height: 100%;
}
.editSpining .ant-spin {
  max-height: 100% !important;
}
.editSpining div > .ant-spin .ant-spin-text {
  top: 42%;
}
.editSpining div > .ant-spin .ant-spin-dot {
  top: 42%;
}
